import React from "react"
import Image1 from "../Images/umasscics.png"
import Image2 from "../Images/msubaroda.jpg"
import Image3 from "../Images/kel.jpg"

function educard(props) {
  const { eduName, eduDegree, eduLink, eduImg, eduCourses} = props
  var Image = Image1
  if (eduImg === 1){
      Image = Image1
  }else if (eduImg === 2){
      Image = Image2
  }else if (eduImg === 3){
    Image = Image3
}
  return (
    <div id="accordion">
    <div class="row">
        <div className="col-lg-2"></div>
        <div className="col-lg-10">
            <div class="card mx-3 my-5" style={{width: "45em"}}>
                <img class="card-img" src={Image} style={{height: "250px"}}/>
                <div class="card-image-overlay">
                    <h5 class="card-title" style={{paddingLeft: "35px",  paddingTop: "15px", color: "#FF8A00" }}>{eduName}
                    <a href={eduLink} className="btn btn-success" style={{ textDecoration: "none", marginRight: "35px", color: "#80cee1", float:"right"}}>
                        Visit!</a>
                    </h5>
                    <p class="card-text" style={{paddingLeft: "35px"}}>{eduDegree}</p>
                    
                </div>
                <div class="card">
                    <div class="card-header" id="headingTwo" >
                    <h6 class="mb-0" style={{display:"block"}}>
                        <button class="btn btn-link collapsed"  data-toggle="collapse" data-target="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo">
                        Courses
                        </button>
                    </h6>
                    </div>
                    <div id="collapseTwo" class="collapse" aria-labelledby="headingTwo" data-parent="#accordion">
                    <div class="card-body">
                    <h6>{eduCourses}</h6>
                    </div>
                    </div>
                </div>
            </div>
        </div>  
    </div>
    </div>
  )
}

export default educard
